.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
    margin-bottom: 40px ;
}

.new-collections h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
    margin-top: 20px;
}

.new-collections hr{
    width: 200px !important;
    height: 6px !important;
    border-radius: 10px;
    background: #252525;
}

.new-collections-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}

.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 30px;
}

